.site-content {
  padding-top: 70px;
  @include bp(tablet) {
    padding-top: 0;
  }
}


.wrapper {
  max-width: $site_width;
  margin: 0 auto;
  padding: 1rem;

  &.content-area {
    padding-bottom: 0;
    padding: 0 1em;
    background: $lightgray;
    min-height: 500px;
  }

  @include bp(tablet) {
    padding: 1rem 1.5rem;
  }

  @include bp(laptopxs) {
    padding: 0;
  }
}

.row {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  box-sizing: border-box;
  &.center {
    webkit-box-align: center;
    -ms-flex-align: center;
    -moz-align-items: center;
    align-items: center;
  }



  .col-xs-2,
  .col-xs-4,
  .col-xs-3,
  .col-xs-5,
  .col-xs-6,
  .col-xs-8,
  .col-xs-9,
  .col-xs-10,
  .col-xs-11,
  .col-xs-12 {
    flex-basis: 100%;
    max-width: 100%;
    background-size: cover;
    background-position: center center;
    // For positioning vertical centered
    &.middle {
      display: flex;
      webkit-box-align: center;
      -ms-flex-align: center;
      -moz-align-items: center;
      align-items: center;
    }
  }

  //  Display Order
  @mixin order($val) {
    -webkit-box-ordinal-group: $val;
    -moz-box-ordinal-group: $val;
    -ms-flex-order: $val;
    -webkit-order: $val;
    order: $val;
  }
  .order-3 {
    @include order(3);
  }
  .order-2 {
    @include order(2);
  }
  .order-1 {
    @include order(1);
  }

  @include bp(tablet) {
    .col-xs-1 {
      flex-basis: 8.333335%;
      max-width: 8.333335%;
    }
    .col-xs-2 {
      flex-basis: 16.66667%;
      max-width: 16.66667%;
    }
    .col-xs-3 {
      flex-basis: 25%;
      max-width: 25%;
    }
    .col-xs-4 {
      flex-basis: 33.33333%;
      max-width: 33.33333%;
    }
    .col-xs-5 {
      flex-basis: 41.66667%;
      max-width: 41.66667%;
    }
    .col-xs-6 {
      flex-basis: 50%;
      max-width: 50%;
    }
    .col-xs-8 {
      flex-basis: 66.66667%;
      max-width: 66.66667%;
    }
    .col-xs-9 {
      flex-basis: 75%;
      max-width: 75%;
    }
    .col-xs-10 {
      flex-basis: 83.33333%;
      max-width: 83.33333%;
    }
    .col-xs-11 {
      flex-basis: 91.666665%;
      max-width: 91.666665%;
    }

    .order-1,
    .order-2,
    .order-3 {
      order: unset;
    }
  }
}

// Gutenberg specific
.wp-block-group {
  .wp-block-column {
    padding: 2em;
  }
}


// Post type specific

.single {

  @include bp( tabletdown ) {
    #primary {
      padding-top: 0;
    }
  }

}
