$font__main: sans-serif;
$font__code: Arial, "Helvetica Neue", Helvetica, sans-serif;
$font__pre: "Courier 10 Pitch", Courier, monospace;
$font__line-height-body: 1.5;
$font__line-height-pre: 1.6;

$font_size: 16px;
$font_family: Arial, "Helvetica Neue", Helvetica, sans-serif;
$font_family_menu: "Helvetica Neue", Helvetica, Arial, sans-serif;
$font_family_headings: 'Open Sans', sans-serif;

// Helper function to calculate ems
@function em($pixels, $context: $font_size) {
  @if (unitless($pixels)) {
    $pixels: $pixels * 1px;
  }

  @if (unitless($context)) {
    $context: $context * 1px;
  }

  @return $pixels / $context * 1em;
}