body,
button,
input,
select,
optgroup,
textarea {
	color: $color__text-main;
	font-family: $font_family;
	font-size: $font_size;
	line-height: $font__line-height-body;
}

// Main link styling
a {
	color: $blue;

	&:visited {
		color: $color__link-visited;
	}
	&:hover,
	&:focus,
	&:active {
		color: $color__link-hover;
	}
	&:focus {
		outline: thin dotted;
	}
	&:hover,
	&:active {
		outline: 0;
	}
}

// @todo set standard margin-bottom
h1, h2, h3, h4, h5, h6 {
	clear: both;
	font-family: $font_family_headings;
	font-weight: 400;

	&.strong {
		font-weight: bold;
	}
}

h2 {
	font-size: 28px;
	font-weight: bold;
	margin-bottom: 2rem;
}

h3 {
	font-size: 25px;
	line-height: 29px;
}

h4 {
	font-size: 18px;
	line-height: 20px;
}


p {
	margin-bottom: 1.5em;
}

hr {
	background-color: $color__background-hr;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

dfn, cite, em, i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}


address {
	margin: 0 0 1.5em;
}

pre {
	background: $color__background-pre;
	font-family: $font__pre;
	@include font-size(0.9375);
	line-height: $font__line-height-pre;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code, kbd, tt, var {
	font-family: $font__code;
	@include font-size(0.9375);
}

abbr, acronym {
	border-bottom: 1px dotted $color__border-abbr;
	cursor: help;
}

mark, ins {
	background: $color__background-ins;
	text-decoration: none;
}

big {
	font-size: 125%;
}

small {
	font-size: em(10);
	display: block;
}


// Helper font classes
.accent {
	font-size: em(16);
	font-weight: 700;
	text-decoration: none;
	&.blue {
		color: $blue;
	}
	&.small {
		font-size: em(12);
		display: block;
		text-align: center;
		margin-top: 1em;
	}
}


