table {
  margin: 0 0 1.5em;
  width: 100%;
}

// Use for all tables
.table-wrapper {
  overflow-x: auto;
  margin: 0 0 20px;
  overflow-y: hidden;
}

// Default table styling - without class
table {

  // general helper classes
  td {
    &.center {
      text-align: center;
    }
    &.break {
      word-break: break-word;
    }
    // For bonus section with 3 lines of text
    &.bonus {
      text-align: center;
      line-height: 1.6;
      word-break: break-word;
      .accent {
        display: block;
        font-weight: 900;
        font-size: em(19);
      }
      strong {
        display: block;
      }
    }



  }

  // full table
  &.full {
    margin-bottom: 20px;
    box-sizing: border-box;
    width: 100%;
    border-collapse: collapse;
    border: 0;
    tr {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-flow: row wrap;
      td {
        padding: 0;
        border: 0;
        background: none;
      }
    }

    tbody {
      tr {
        padding: 10px;
        padding-left: 30px;
        margin: 0 2px 1rem;
        box-shadow: 0px 0px 6px #0000001F;
        background: white;
        border-radius: 3px;

        @include bp(tablet) {
          max-width: 100%;
          padding: 10px 20px;
        }

        td {
          flex: 0 0 50%;
          &.action {
            flex: 0 0 100%;
            max-width: 100%;
          }
          @include bp(tablet) {
            flex: 1 1 0px;
          }

          .number {
            position: absolute;
            display: flex;
            width: 20px;
            top: -1px;
            bottom: -1px;
            left: -1px;
            justify-content: center;
            align-items: center;
            font-size: em(14);
            font-weight: 700;
            color: #fff;
            text-align: center;
            text-indent: 0;
            background: $darkblue;
          }

          &.number {
            font-weight: bold;
            font-size: em(20);
          }
          // Make it wide only on mobile
          &.wide-xs {
            @include bp( tabletdown ) {
              display: block;
              flex: 1 1 100%;
              margin: 1em 0;
            }
          }

          &.wide {
            display: block;
            flex: 1 1 100%;
            background: $lightgray;
            //justify-content: space-between;
            margin-top: 1em;
            span {
              width: 100%;
              line-height: 1.2;
              font-size: em(12);
              padding: em(8);
              position: relative;
              display: flex;
              align-items: center;
              justify-content: center;
              vertical-align: top;
              margin-right: em(24);

              strong  {
                display: inline-block;
                width: 45%;
                color: $blue;
              }
              div {
                display: inline-block;
                width: 50%;
              }

            }

            @include bp(tablet) {
              display: flex;

              span {
                width: auto;
                font-size: em(12);
                padding: .7em;
                padding-left: 30px;
                margin-bottom: 0;
                display: inline-block;

                strong {
                  display: block;
                  width: auto;
                }
                div {
                  display: inline-block;
                  width: 100%;
                }

                &:first-child {
                  margin-left: em(24);
                }
              }

            }

          }
        }

      }
    }
  }

  &.short {
    margin-bottom: 20px;
    box-sizing: border-box;
    width: 100%;
    border-collapse: collapse;
    border: 0;
    tr {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-flow: row wrap;
      td {
        padding: 0;
        border: 0;
        background: none;
      }
    }
    thead {
      display: none;
      @include bp(tablet) {
        margin-bottom: 1em;
        background: none;
        display: block;
        font-size: 10px;
        tr {
          display: flex;
          padding: 10px 20px;
          justify-content: center;
          align-items: center;
          flex-flow: row wrap;
          th {
            flex: 0 0 50%;
            &.action {
              flex: 0 0 100%;
            }
            @include bp(tablet) {
              flex: 1 1 0px;
              &.number {
                max-width: 25px;
              }
            }
          }
        }

      }
    }
    tbody {
      tr {
        max-width: 380px;
        padding: 10px 20px;
        margin: 0 2px 1rem;
        box-shadow: 0px 0px 6px #0000001F;
        background: white;
        border-radius: 3px;
        @include bp(tablet) {
          max-width: 100%;
        }
        td {
          flex: 0 0 50%;

          .number {
            position: absolute;
            display: flex;
            width: 30px;
            height: 30px;
            top: -1px;
            bottom: -1px;
            left: -1px;
            justify-content: center;
            align-items: center;
            font-size: em(14);
            font-weight: 700;
            color: #fff;
            text-align: center;
            text-indent: 0;
            background: $darkblue;
          }

          &.number {
            flex: 0 0 10%;
          }

          &.logo {
            flex: 0 0 40%;
          }

          &.wide {
            display: block;
            margin: 1em 0;
            flex: 0 0 100%;
          }

          &.data-title {

            &:before {
              content: attr(data-title);
              display: block;
              margin: 1em 0;
              font-weight: bold;
              font-size: em(12);
              line-height: 1.2;
              color: $darkblue;
            }

            @include bp(tablet) {
              &:before {
                content: none;
              }

            }

          }

          &.action {
            flex: 0 0 100%;
          }

          @include bp(tablet) {

            flex: 1 1 0px;

            .number {
              width: 18px;
              height: auto;
            }

            &.wide {
              display: none;
            }

            &.logo {
              flex: 1 1 0px;
            }

            &.number {
              max-width: 25px;
            }

          }

          &.number {
            font-weight: bold;
            font-size: em(20);
          }

        }

      }
    }
  }

  // Standard non-responsive
  &.standard {
    box-shadow: 0px 0px 6px #0000001F;
    border-radius: 3px;
    width: 100%;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
    border-collapse: separate;
    border-spacing: 0;

    thead {
      background: $darkblue;
      color: white;
      font-weight: 500;
      tr {
        th {
          padding: 0.786rem;
          font-weight: normal;
          text-align: center;
          vertical-align: middle;
          border: 1px solid $border_color;
        }
      }
    }

    tbody {
      background: white;
      tr {
        &:nth-child(even) {
          td {
            background: $lightgray;
          }
        }
        td {
          padding: 0.786rem;
          text-align: center;
          vertical-align: middle;
          min-width: 100px;
          border: 1px solid $border_color;
          & + td {
            border-left-width: 0;
          }
          &.small {
            font-size: 10px;
            text-align: left;
          }
          .terms-container {
            min-width: 200px;
          }
          &.accent {
            font-size: 16px;
            font-weight: bold;
            &.blue {
              color: $blue;
            }
          }
          img {
            min-width: 150px;
          }
          @include bp(tabletlandscape) {
            min-height: 100px;
            min-width: 150px;
          }
        }
        // prevent double border
        & + tr {
          td {
            border-top-width: 0;
          }
        }
      }
    }
  }

  // General
  ul {
    margin: 0;
    padding-left: 1em;
  }

}

