html {
  box-sizing: border-box;
}

*,
*:before,
*:after { /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see https://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit;
}

@import "lists";
@import "tables";

// Boxes
.box {
  display: block;
  width: 100%;
  background: white;
  box-shadow: 0px 0px 6px #0000001F;
  border-radius: 3px;
  padding: em(16);
  &.gray {
    background: $lightgray;
    box-shadow: none;
  }
  &.dark {
    background: $blue_opac;
    box-shadow: none;
  }
  &.simple {
    background: none;
    box-shadow: none;
    padding: em(16) 0;
  }
}

// Bonus text stacking
.bonus {
  text-align: center;
  line-height: 1.6;
  .accent {
    display: block;
    font-weight: 900;
    font-size: em(19);
  }
  strong {
    display: block;
  }

  &.big {
    line-height: 1.4;
    .accent {
      font-size: 1.5em;
      font-weight: 200;
    }
    strong {
      font-size: 2em;
    }
  }
}

// Additional icons
.icon {
  vertical-align: top;

  @include bp(tablet) {
    &:before {
      content: '';
      position: absolute;
      width: 25px;
      height: 25px;
      // top: em(10);
      background: url('../icons/table-sprite.png') no-repeat 0 0;
      left: 0;
      display: block;
      line-height: 1em;
      text-indent: 0;
    }

    &.eligibility {
      &:before {
        background-position-x: 0;
      }
    }

    &.deposit {
      &:before {
        background-position-x: -25px;
      }
    }

    &.coin {
      &:before {
        background-position-x: -50px;
      }
    }

    &.seven {
      &:before {
        background-position-x: -75px;
      }
    }

    &.roulette {
      &:before {
        background-position-x: -100px;
      }
    }

    &.scissors {
      &:before {
        background-position-x: -125px;
      }
    }
  }

}

// Hamburger icons
.hamburger {
  display: inline-block;

  span {
    background: #F7F7F7;
    border-radius: 2px;
    width: 24px;
    height: 4px;
    opacity: 1;
    display: block;
    margin-bottom: 5px;
    -webkit-transition: width 300ms; /* Safari prior 6.1 */
    transition: 0.4s;

    &:last-child {
      margin-bottom: 0;
      transition: 0.4s;
    }

  }

  &.open {
    span {
      &:first-child {
        -webkit-transform: rotate(-45deg) translate(-8px, 5px);
        transform: rotate(-45deg) translate(-8px, 5px);
      }
      &:nth-child(2) {
        opacity: 0;
      }
      &:last-child {
        -webkit-transform: rotate(45deg) translate(-8px, -5px);
        transform: rotate(45deg) translate(-8px, -5px);
      }
    }

  }

  &.sort {

    width: 24px;
    transition: width 300ms;

    span {
      background: #F7F7F7;
      border-radius: 2px;
      height: 4px;
      opacity: 1;
      display: block;
      margin-bottom: 5px;
      -webkit-transition: width 300ms; /* Safari prior 6.1 */
      transition: width 300ms;

      &:first-child {
        width: 24px;
        transition: width 300ms;
      }

      &:nth-child(2) {
        width: 18px;
      }

      &:last-child {
        width: 12px;
        margin-bottom: 0;
        transition: width 300ms;
      }
    }

    &.active {
      span {
        &:first-child {
          width: 12px;
          transition: width 300ms;
        }

        &:last-child {
          width: 24px;
          transition: width 300ms;
        }
      }
    }

  }
}

// Main logo text - transforms normal text in logo
a.logo-text,
.logo-text {
  text-transform: uppercase;
  text-decoration: none;
  color: white;
  font-size: em(26);
  .green {
    color: $green;
  }
}

// Logo listings
.logos {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  img {
    height: 38px;
    width: auto;
    margin: em(8);
    max-width: 100%;
    padding: 0 5px;
  }
  &.small {

    justify-content: flex-start;
    .title {
      font-size: .75em;
      font-weight: 600;
    }
    img {
      height: 20px;
      display: inline-block;
      vertical-align: middle;
      margin: 0;
      margin-left: 5px;
    }
    @include bp(tabletdown) {
      text-align: center;
      margin-bottom: .5em;
      justify-content: center;
      .title {
        display: block;
        width: 100%;
        margin-bottom: .5em;
      }
    }
  }
}

// Rating
.rating {
  display: block;
  width: 96px;
  height: 15px;
  margin: 0 auto;
  background: url('../images/star-blank.png') repeat-x 0 0 / auto 100%;
  &:before {
    content: '';
    position: static;
    display: block;
    height: 100%;
    background: url('../images/star-full.png') repeat-x 0 0 / auto 100%;
    width: 82%; // Default star
  }
  &.r5 {
    &:before {
      width: 100%;
    }
  }
  &.r49 {
    &:before {
      width: 98%;
    }
  }
  &.r48 {
    &:before {
      width: 96%;
    }
  }
  &.r47 {
    &:before {
      width: 94%;
    }
  }
  &.r46 {
    &:before {
      width: 92%;
    }
  }
  &.r45 {
    &:before {
      width: 90%;
    }
  }
  &.r44 {
    &:before {
      width: 88%;
    }
  }
  &.r43 {
    &:before {
      width: 86%;
    }
  }
  &.r42 {
    &:before {
      width: 84%;
    }
  }
  &.r41 {
    &:before {
      width: 82%;
    }
  }
  &.r40 {
    &:before {
      width: 80%;
    }
  }

  &.single {
    display: inline-block;
    width: 10px;
    height: 10px;
    &:before {
      width: 100%;
    }
  }

  // Medium stars
  &.medium {
    width: 64px;
    height: 10px;
  }

  // Small stars
  &.small {
    width: 52px;
    height: 8px;
  }

}

// Small reusable elements
.license-number {
  display: inline-block;
  text-align: center;
  padding: 1em;
  border: 1px solid $blue;
  border-radius: 3px;
  strong {
    color: $blue;
    display: block;
  }

  &.small {
    padding: 0;
    border: none;
    strong {
      display: inline;
    }
    @include bp(tabletdown) {
      width: 100%;
      margin: .5em;
      strong {
        display: block;
      }
    }

  }

}

.rating-box {

  display: inline-block;
  text-align: center;
  padding: .5em;
  border: 1px solid #EFEFEF;
  border-radius: 3px;

  .rating-number {
    font-weight: bold;
    color: $green;
    font-size: 2em;
  }

  &.compact {
    border: none;
    background: #D07B6612 0% 0% no-repeat padding-box;
    padding: .2em;
    .rating-number {
      display: inline;
      font-size: em(13);
    }
  }

  &.detailed {
    border: none;
    padding: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  ul {
    display: table-cell;
    position: relative;
    margin: 0 auto;
    padding: 0;
    list-style-type: none;
    vertical-align: middle;
    text-align: left;
    li {
      float: left;
      width: 100%;
      margin-bottom: 1em;
      .label {
        float: left;
        width: 35%;
        font-size: em(10px);
        font-weight: 600;
      }
      .rating {
        float: left;
        margin-top: em(2);
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .overall {
    display: inline-block;
    white-space: nowrap;
    background: $darkblue;
    padding: 1.5em 1em;
    .title {
      font-size: em(10px);
      display: block;
    }
    .score {
      font-size: em(18px);
      color: $blue_air;
      .green {
        color: $green;
      }
    }
  }

}