ul, ol {
  margin: 0 0 1.5em 3em;
  margin-left: 0;
}

ul {
  list-style: disc;
}

ol {
  list-style: decimal;
}

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 1.5em;
}

dt {
  font-weight: bold;
}

dd {
  margin: 0 1.5em 1.5em;
}

.content-area {
  ul.custom {

    list-style: none;
    li::before {
      content: "•";
      color: $green;
      display: inline-block;
      width: 1em;
      font-size: 1.2em;
      margin-left: -1em
    }
  }
}

ul.listing-checks {
  margin: 0;
  padding: 0;
  padding-left: 1em;
  list-style-type: none;

  li {
    margin-bottom: em(16);
    font-size: em(12);
    &:before {
      content: '';
      display: inline-block;
      transform: rotate(45deg);
      height: 12px;
      width: 5px;
      border-bottom: 2px solid $green;
      border-right: 2px solid $green;
      margin-right: 1em;
    }
    &:last-child {
      margin-bottom: 0;
    }

  }
}

// used for listing in tables
ul.listing-compact {
  margin: 0;
  //padding: 0;
  list-style-type: none;
  li {
    margin-bottom: em(14);
    font-size: em(12);
    &:last-child {
      margin-bottom: 0;
    }
  }
}

// listing for resume/hightlight/testimonial boxes
ul.listing-resumes {
  margin: 2em 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  li {
    padding: 1em 2em;
    font-size: em(18);
    margin-bottom: 1em;
    width: 100%;
    background: white;
    text-align: center;
    box-shadow: 0px 0px 6px #0000001F;

    @include bp(tablet) {
      max-width: 350px;
    }
  }
  @include bp(tablet) {
    padding: 2em 0;
  }
}

// filters above table
ul.listing-filters {
  margin: 0;
  padding: 0;
  margin-bottom: 1em;
  list-style-type: none;
  background: $darkblue;
  padding: .5em;
  text-align: center;
  box-shadow: 0px 0px 6px #00000029;
  display: flex;
  justify-content: space-around;

  li {
    padding: .5em;
    position: relative;

    &:hover {
      border-color: $blue;
      transition: linear 150ms;
      a {
        color: $blue;
        transition: linear 150ms;
        &:before {
          transition: linear 150ms;
          filter: invert(55%) sepia(34%) saturate(896%) hue-rotate(150deg) brightness(106%) contrast(97%);
        }
      }

    }

    &.active {
      border-color: white;
      background: white;
      transition: linear 150ms;
      a {
        color: $darkblue;
        transition: linear 150ms;
        &:before {
          transition: linear 150ms;
          filter: invert(55%) sepia(34%) saturate(896%) hue-rotate(150deg) brightness(106%) contrast(97%);
        }
      }
    }

    a {
      color: white;
      text-decoration: none;
      font-weight: bold;
      font-size: 1em;
      opacity: 0.77;
      transition: linear 150ms;
      &.active {
        color: $blue;
        opacity: 1;
      }

      &:active, &:focus {
        outline: 0;
        border: none;
        -moz-outline-style: none;
      }

      @include bp(tablet) {

        font-size: em(18);

        &:before {
          content: '';
          position: absolute;
          width: 25px;
          height: 25px;
          left: 10px;
          top: 10px;
          display: block;
          line-height: 1em;
          text-indent: 0;
        }

        // Icons
        &.trophy {
          &:before {
            background: url('../icons/trophy.svg') no-repeat 0 0;
          }
        }
        &.star {
          &:before {
            background: url('../icons/star.svg') no-repeat 0 0;
          }
        }
        &.percentage {
          &:before {
            background: url('../icons/discount.svg') no-repeat 0 0;
          }
        }
        &.free {
          &:before {
            background: url('../icons/casino.svg') no-repeat 0 0;
          }
        }
        &.new {
          &:before {
            background: url('../icons/badge.svg') no-repeat 0 0;
          }
        }

      }

    }

    margin: .5em 0;
    border: 1px solid white;
    border-radius: 30px;

    @include bp(tablet) {
      margin: .5em;
      border: 1px solid $blue_air;
      border-radius: 30px;
      padding-left: 35px;
    }

  }

  @include bp(tablet) {
    background: $darkblue;
    justify-content: space-between;
    padding: 0 2em;
  }
}

ul.listing-cta {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: inline-block;
  width: 100%;
  font-size: em(14);
  .name {
    float: left;
    width: 60%;
    font-weight: bold;
  }
  .value {
    float: right;
    width: 40%;
    text-align: left;
    word-break: break-all;
  }
  li {
    float: left;
    width: 100%;
    margin-bottom: em(14);
    @include bp(tablet) {
      &:last-child {
        margin-bottom: 0;
      }
    }

  }
}

// Listing for link boxes
ul.listing-linkboxes {

  padding: 0;
  list-style-type: none;
  display: flex;
  flex-flow: row wrap;
  //justify-content: space-between;
  margin: 2em -10px;

  li {

    flex-basis: 42%;
    max-width: 42%;
    margin: 0 10px 20px;

    border-radius: 3px;
    text-align: center;

    .svg-holder {
      margin: 0 auto;
      margin-bottom: em(20);
    }

    a {
      display: block;
      height: 100%;
      width: 100%;
      background: white;
      box-shadow: 0px 0px 6px #0000001F;
      padding: em(32) em(16);
      text-decoration: none;
      color: $darkblue;
      font-size: em(13);
      transition: all 200ms;

      &:hover {
        box-shadow: 0px 0px 6px $blue;
        transition: all 200ms;
      }
    }

    @include bp(tablet) {
      flex-basis: 15%;
      max-width: 15%;
      margin: 0 10px 20px;
    }
  }
}

// Listing for pros and cons
ul.list-procons {
  padding: 0;
  margin: 0;

  position: relative;
  padding-left: 65px;
  list-style: none;

  @include bp(tabletdown) {
    margin: 1em 0;
  }

  li {
    flex: 0 0 50%;
    position: relative;
    padding-left: 15px;
    margin-bottom: 5px;
    font-size: .875em;
    @include bp(tabletdown) {
      flex: 0 0 100%;
    }
  }

  &.pros {
    display: flex;
    flex-flow: row wrap;
    &:before {
      content: '';
      position: absolute;
      width: 36px;
      height: 72px;
      top: 50%;
      left: 0;
      margin-top: -36px;
      background: url('../icons/pros.svg') no-repeat 0 0 / 100% 100%;
    }
    li {
      &:before {
        content: '';
        display: inline-block;
        transform: rotate(45deg);
        height: 12px;
        width: 5px;
        border-bottom: 2px solid $green;
        border-right: 2px solid $green;
        margin-right: 1em;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &.cons {
    li {
      &:before {
        content: 'x';
        display: inline-block;
        height: 12px;
        color: red;
        margin-right: .5em;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
    &:before {
      content: '';
      position: absolute;
      width: 36px;
      height: 72px;
      top: 50%;
      left: 0;
      margin-top: -36px;
      background: url('../icons/cons.svg') no-repeat 0 0 / 100% 100%;
    }
  }
}

// Listing for additional gameboxes
ul.listing-gameboxes {

  padding: 0;
  list-style-type: none;
  display: flex;
  flex-flow: row wrap;
  //justify-content: space-between;
  margin: 2em -10px;

  li {

    flex-basis: 100%;
    max-width: 100%;
    display: flex;
    background: white;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0 10px 20px;
    padding: 1em;
    box-shadow: 0px 0px 6px #0000001F;
    border-radius: 3px;

    .left {
      flex-basis: 40%;
      max-width: 40%;
    }

    .right {
      flex-basis: 55%;
      max-width: 55%;
    }

    .listing-cta {
      li {
        flex-basis: 100%;
        max-width: 100%;
        box-shadow: none;
        margin: 0;
        padding: em(8);
        font-size: em(10);
        .rating {
          margin-left: em(8);
        }
      }
    }

    a {
      margin-top: 2em;
    }

    @include bp(tablet) {
      flex-basis: 30%;
      max-width: 30%;
      margin: 0 em(20) em(40);
    }
  }
}