button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	border: 1px solid;
	border-color: $color__border-button;
	border-radius: 3px;
	background: $color__background-button;
	color: rgba(0, 0, 0, .8);
	@include font-size(0.75);
	line-height: 1;
	padding: .6em 1em .4em;

	&:hover {
		border-color: $color__border-button-hover;
	}

	&:active,
	&:focus {
		border-color: $color__border-button-focus;
	}
}

// general class button
.button {
	display: block;
	border-radius: 3px;
	width: 100%;
	text-transform: uppercase;
	text-align: center;
	text-decoration: none;
	padding: 1em;
	&.green {
		color: white;
		background: $green;
		&:hover {
			background: darken( $green, 10% );
		}
	}
	&.blue {
		color: white;
		background: $blue;
		&:hover {
			background: darken( $blue, 10% );
		}
	}
}

// Button holder with background opacity
.button-holder {
	@include bp(tablet) {
		left: 0;
		width: 100%;
		box-sizing: border-box;
		display: block;
		padding: 3em 1.5em;
	}

	bottom: 0;
	position: absolute;
	width: 100%;
	box-sizing: border-box;
	display: block;
	padding: 0 1em;
	background: rgba(0, 0, 0, 0.8);
}
