// Main megamenu navigation
.megamenu-wrapper {
  top: 70px;

  // Native - mobile styling
  @include bp(tabletdown) {
    li {
      transition: max-height .15s, padding .15s, margin .15s;
      max-height: 600px;
      padding: 10px;
      overflow: hidden;
      // Small icon on the left
      .parent-icon {
        position: absolute;
        width: 64px;
        height: 64px;
        top: -8px;
        bottom: -5px;
        left: 0;
        border-radius: 30px;
        background: white;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain; // change to 80% if it doesnt look good
      }
      &.has-dd {
        // Add the plus sign next to the menu title
        &:before {
          content: '';
          position: absolute;
          width: 28px;
          height: 28px;
          top: 27px;
          right: 27px;
          border-radius: 50%;
          background: white url('../icons/plus-clean.svg') no-repeat center center / 16px 16px;
          z-index: 1;
          transition: transform .15s .15s, background .15s;
        }
        position: relative;
        // When the submenu is fully open
        &.nav-active {
          &:before {
            background: white url('../icons/left-arrow.svg') no-repeat center center / 16px 16px;
          }
          .site-nav-dropdown {
            overflow: visible;
            max-height: 600px;
            padding-top: 15px;
            transition: max-height .15s .15s, padding .15s .15s;
            .inner {
              .inner-title {
                span {
                  &:before {
                    content: '';
                    position: absolute;
                    width: 100vw;
                    height: 1px;
                    top: 50%;
                    right: 100%;
                    margin-right: 15px;
                    background: #406580;
                  }
                }
              }
            }
          }
        }
      }
      a {
        position: relative;
        display: block;
        padding: 12px 50px 12px 75px;
        border-radius: 50px;
        font-size: 17px;
        font-weight: 700;
        color: white;
      }
    }
    .site-nav-dropdown {
      max-height: 0;
      overflow: hidden;
      transition: max-height .15s .15s, padding .15s .15s, margin .15s .15s;
      .inner {
        height: auto;
        .inner-title {
          display: block;
          font-size: 10px;
          font-weight: 300;
          font-style: italic;
          color: #89b5d5;
          text-align: right;
          overflow: hidden;
          span {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            z-index: 2;
          }
          small {
            display: none;
          }
        }
        ul {
          font-size: 0;
          text-align: center;
          position: relative;
          li {
            display: inline-block;
            width: 90px;
            padding: 0 5px;
            margin-bottom: 15px;
            vertical-align: top;
            a {
              display: block;
              font-size: 12px;
              padding: 0;
              line-height: 1.2;
              font-weight: 600;
              color: #fff;
              text-align: center;
              background: none;
              .icon {
                content: '';
                display: block;
                width: 58px;
                height: 58px;
                margin: 4px auto;
                box-shadow: 0px 0px 9px #FFFFFF29;
                border-radius: 8px;
                background: linear-gradient(to bottom, rgba(46, 125, 187, 1) 0%, rgba(43, 79, 117, 1) 100%);
                background-repeat: no-repeat;
                background-position: center center;
                background-size: 70% 70%;
              }
            }
          }
        }
      }

    }

    // Disable other elements when you open a drilldown
    &.drilldown-active > li:not(.nav-active) {
      max-height: 0;
      padding-top: 0;
      padding-bottom: 0;
      margin-top: 0;
    }
  }

  // > Tablet and Desktop
  @include bp(tablet) {
    li {
      a {
        .icon {
          &:before {
            background: none;
          }
        }
      }
      float: left;
      &.has-dd {
        &:hover {
          // Show the dropdown on hover
          .site-nav-dropdown{
            opacity: 1;
            visibility: visible;
          }
          .ul-default {
            opacity: 1;
            visibility: visible;
          }
        }
      }
      a {
        .parent-icon {
          display: none;
        }
      }
    }
    .site-nav-dropdown {
      position: absolute;
      display: flex;
      width: 600px;
      left: 50%;
      top: 100%;
      padding: 10px 0;
      margin: 14px 0 0 -335px;
      border-radius: 4px;
      box-shadow: 0 3px 7px rgba(0, 0, 0, .35);
      opacity: 0;
      visibility: hidden;
      background: #fff;
      transition: opacity .3s, visibility .3s;
      li {
        a {
          color: $darkblue;
          .icon {
            &:before {
              background: none;
            }
          }
        }
      }
      .inner {
        flex: 1 1 100%;
        padding: 0 10px;
        // The title for each column
        .inner-title {
          background: linear-gradient(to right, $darkblue 0%, $blue 100%);
          position: relative;
          display: block;
          padding: 10px 80px 10px 15px;
          margin-bottom: 15px;
          border-radius: 4px;
          font-size: 18px;
          line-height: 1;
          font-weight: 700;
          transition: opacity .3s;
          span {
            position: relative;
            display: block;
            z-index: 2;
          }
          small {
            position: relative;
            display: block;
            font-size: 12px;
            font-weight: 300;
            z-index: 2;
          }
          .inner-icon {
            content: '';
            position: absolute;
            width: 60px;
            height: 100%;
            top: 0;
            right: 0;
            background-size: contain;
            background-position: right center;
            background-repeat: no-repeat;
          }
        }

        ul {
          li {
            float: none;
            margin-top: 8px;
            &:first-child {
              margin-top: 0;
            }
            a {
              color: $darkblue;
              position: relative;
              display: inline-block;
              padding-left: 25px;
              vertical-align: middle;
              font-weight: 600;
              transition: padding .3s, color .3s;
              .icon {
                display: none;
              }
              &:before {
                content: '';
                position: absolute;
                width: 12px;
                height: 12px;
                top: 7px;
                left: 0;
                background: url('../icons/right-arrow.svg') no-repeat 0 0 / contain;
                transition: transform .3s;
              }
              &:hover {
                padding-left: 30px;
                color: $blue;
                &:before {
                  transform: translateX(5px) scale(1.25);
                }
              }
            }
          }
        }
      }
      &:before {
          content: '';
          position: absolute;
          width: 0;
          height: 0;
          bottom: 100%;
          left: 56%;
          margin-left: -12px;
          border-width: 0 12px 8px 12px;
          border-style: solid;
          border-color: transparent transparent #fff transparent;
          z-index: 2;
        }
    }
  }
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
  display: block;
}

@include bp(tablet) {

  .menu-toggle {
    display: none;
  }

  .main-navigation ul {
    display: block;
  }

}

.comment-navigation,
.posts-navigation,
.post-navigation {

  .site-main & {
    margin: 0 0 1.5em;
    overflow: hidden;
  }

  .nav-previous {
    float: left;
    width: 50%;
  }

  .nav-next {
    float: right;
    text-align: right;
    width: 50%;
  }
}

ul.menu {
  &.page-menu {
    padding: 0;
    margin: 1em 0;
    display: flex;
    justify-content: center;
    flex-flow: row wrap;
    li {
      padding: 1em 2em;
      a {
        text-decoration: none;
        color: $darkblue;
      }
    }
  }
}

// Footer navigation
.footer-navigation {
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    li {
      margin-bottom: 1.2em;
      a {
        color: $blue;
        text-transform: capitalize;
        text-decoration: none;
      }
    }

    @include bp(tablet) {
      padding: 0 1em;
    }

  }
}

// Simple inline navigation with separator
.inline-navigation {
  ul {
    list-style: none;
    margin: 0;
    padding: 0;

    li {
      margin-bottom: 1.2em;
      display: inline;
      font-size: em(12);
      font-weight: bold;

      a {
        color: $green;
        text-decoration: none;
        &:hover {
          text-decoration: underline;
        }
      }

      &:after {
        content: '';
        border-right: 1px solid $green;
        width: 1px;
        padding-left: em(10);
        margin-right: em(10);
      }

      &:last-child {
        &:after {
          content: none;
        }
      }
    }

  }
}